/* Phase Parameter Cards Style */

.flashCards{
    display: flex;
    justify-content: space-evenly;
}

.modebar { display: none !important; }

.card{
    display: inline-block;
    margin: 0.4% 1.5%;
    background-color: aliceblue;
    border: 2px solid gray;
    text-align: center;
}

.card-body{
    display: flex;
    flex-direction: column;
    width: auto;
    height: auto;
    padding: 1%;
}

table{
    margin-top: 1%;
}
/* Device Control section */
.stateInput label{
    margin: 0 0 0 10px;
    font-size: 100%;
}

.stateInput{
    display: table;
}

.mainSection{
    display: inline-block;
    margin: 1.5% 0;
    padding: 0;
}

/* Graph section */
.graph{
    height: auto;
    width: auto;
    margin: 1% 6%;
    background-color: #f0f8ff; 
    border-radius: 10px;
    border: 2px solid gray;
    padding: 1%
}

/* Adjust size of graph component automatically*/
@media (max-width: 1115px){
    .graph {
        width: auto;
        height: auto;
        margin: 1% 5%;;
        margin-bottom: 50px;
        padding: 1.5%;
    }
    .flashCards{
        margin: 0 4%;
    }
    body{
        max-width: 100vw;
    }
}

@media (max-width: 667px) and (max-height: 375px) {
    .card{
        width: 36vh !important;
    }
  }

@media screen and (min-width: 1024px) and (min-height: 768px) {
  
    .graph {
      height: auto;
      margin-bottom: 5%;
    }
    .flashCards{
        margin: 0 1%;
    }
    #chart1{
        height: 48vh;
    }
    #chart2{
        height: 48vh;
   }
  }

  @media screen and (max-height: 768px) {
  
   #chart1{
        height: 45vh;
   }
   #chart2{
        height: 45vh;
    }
  }